<template>
    <div id="dashboard">
        
        <ul class="collection with-header">
            <li class="collection-header"><h2>Open Orders</h2></li>
            <li v-for="order in orders" v-bind:key="order.id" class="collection-item">
                {{order.description}}<br /> at &nbsp; {{order.location}} <br /> for {{order.orderedBy}}
            </li>
        </ul>
    <div class="fixed-action-btn">
        <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
        </router-link>
    </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'dashboard',
    data() {
        return {
            orders: []
        }
    },
    created () {
        db.collection('orders').get().then
        (querySnapshot => {
            querySnapshot.forEach(doc => {
                console.log(doc.data)
                const data = {
                    'id': doc.id,
                    'orderId': doc.data().orderId,
                    'description': doc.data().description,
                    'orderedBy': doc.data().userId,
                    'location': doc.data().location
                }
                this.orders.push(data)
            })
        })
    }
}
</script>



