<template>
    <div id="new-meal">
        
        <ul class="collection with-header">
            <li class="collection-header"><h2>New Meal</h2></li>
            <li v-for="item in items" v-bind:key="item.id" class="collection-item">
                <strong>{{item.category}}:</strong> {{item.itemName}}
                <span style="float:right;"><span style="float: left;"><label for="quantity">Quantity:</label></span><input type="number" id="quantity" name="quantity"
       min="0" max="100"></span>
       <div style="clear:both;"></div>
            </li>
        </ul>
        <button class="btn btn-warning">Submit</button>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'new-meal',
    data() {
        return {
            items: []
        }
    },
    created () {
        db.collection('items').get().then
        (querySnapshot => {
            querySnapshot.forEach(doc => {
                console.log(doc)
                const data = {
                    'id': doc.id,
                    'category': doc.data().category,
                    'itemId': doc.data().itemId,
                    'itemName': doc.data().itemName
                }
                this.items.push(data)
            })
        })
    }
}
</script>




